// Page Sections
.page-section {
  max-width: @section-max-width;
  margin: 0 auto 35px;
  padding: 0 20px;
  &--gray {
    background-color: @gray;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  &--narrow {
    max-width: 1010px;
  }
}
.section-header {
  background-color: @brand-primary;
  color: #fff;
  margin-bottom: 15px;
  padding: 5px 20px;
  &--center {
    text-align: center;
  }
  &--flex {
    @media @tablet {
      display: flex;
    }
  }
  &__title {
    font-size: 18px;
    line-height: 1.78;
    text-transform: uppercase;
  }
}

// Flex Row
.flex-col {
  margin-bottom: 20px;
}
@media @tablet {
  .flex-row {
    display: flex;
  }
  .flex-col {
    flex: 1 1 auto;
    margin: 0 1%;
    width: 48%;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  .flex-col-1-3 {
    width: 31.33%;
  }
  .flex-col-2-3 {
    width: 64.66%;
  }
  .flex-col-1-4 {
    width: 23%;
  }
  .flex-col-3-4 {
    width: 73%;
  }
  .flex-col-equal {
    width: 50%;
  }
  .flex-col-main {
    width: 66.66%;
  }  
  .flex-col-aside {
    width: 33.33%;
  }
}
@media @tablet-landscape {
  .flex-col-main {
    width: 75%;
  }  
  .flex-col-aside {
    width: 25%;
  }
}
// Flex Column
.flex-column {
  display: flex;
  flex-direction: column;
}

// Width Helpers
@media @tablet {
  .width-1-3 {
    width: 33.33%;
  }
  .width-100,
  .width-100 + .ui-selectmenu-button {
    min-width: 100px;
    max-width: 100px;
  }
  .width-125 {
    min-width: 125px;
    max-width: 125px;
  }
  .width-135 {
    min-width: 135px;
    max-width: 135px;
  }
  .width-200 {
    min-width: 200px;
    max-width: 200px;
  }
  .width-240 {
    min-width: 240px;
    max-width: 240px;
  }
}
@media @tablet-portrait {
  .flex-col-1-3 .width-1-3 {
    width: auto;
  }
}

// Flex
.flexGrow {
  flex-grow: 1 !important;
}
.flexGrow2 {
  flex-grow: 2 !important;
}
.flexGrow1-5 {
  flex-grow: 1.5 !important;
}
.flexGrow0-5 {
  flex-grow: .5 !important;
}
.flexGrowContent {
  flex: 0 0 auto;
  width: auto;
}