.searchbox-section {
  max-width: @section-max-width;
  margin: auto;
  position: relative;
  z-index: 1;
}
.searchbox-tabs-nav {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  &__item {
    flex: 1 1 auto;
  }
}
.searchbox-tabs-nav__item-title {
  background-color: fade(@brand-primary, 80%);
  color: #fff;
  display: block;
  font-size: 14px;
  line-height: 20px;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  &:hover {
    color: #fff;
  }
  .ui-tabs-active & {
    background-color: rgba(255, 255, 255, 0.8);
    color: @brand-primary;
  }
}
.searchbox-tabs {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px 25px 15px;
}
.searchbox-tab:not(:first-child) {
  display: none;
}
@media @tablet {  
  .searchbox {
    position: absolute;
    bottom: 15px;
    left: 135px;
  }
  .searchbox-tabs-nav {
    display: block;
    width: 110px;
    position: absolute;
    top: 0;
    right: 100%;
  }
  .searchbox-tabs {
    width: 300px;
    height: 405px;
  }
}