// Labels
label {
  color: @brand-primary;
  display: block;
  font-size: 14px;
  line-height: 1.79;
  .form-field--inline > & {
    display: inline-block;
    vertical-align: middle;
    width: auto;
  }
}

// Form Controls
.form-control {
  border: 1px solid #d8d8d8;
  color: #000;
  font-size: 14px;
  line-height: 20px;
  padding: 5px 10px;
  width: 100%;
  height: 32px;
  &:focus {
    border-color: fade(@brand-primary, 50%);
    outline: 1px solid fade(@brand-primary, 50%);
  }
  &--searchbox {
    background-color: #f4f4f4;
    border-color: @brand-primary;
  }
  .placeholder(#000);
  &--small {
    font-size: 12px;
  }
}
.datepicker-control {
  background-image: data-uri('../img/icon-calendar.png');
  background-position: 100% 0;
  background-repeat: no-repeat;
  cursor: pointer;
  padding-right: 40px;
}

// Custom Form Controls
.searchbox-age {
  width: 130px;
}
#security-number {
  width: 145px !important;
}
#area-code {
  width: 125px !important;
}
#sort-activity + .ui-selectmenu-button {
  width: 160px !important;
}
#currency + .ui-selectmenu-button {
  width: 80px !important;
}
.form-field--title {
  min-width: 85px !important;
  max-width: 85px;
  flex: 0 0 85px !important;
}

// Form Fields
.form-field {
  margin-bottom: 10px;
  position: relative;
  &--inline {
    align-items: center;
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    white-space: nowrap;
    & > * {
      flex: 1 1 auto;
      margin-left: 10px;
    }
  }
  &--right {
    text-align: right;
  }
  &--center {
    margin-top: 20px;
    text-align: center;
  }
  &--small {
    font-size: 12px;
  }
  &--travel-agents-button {
    margin-bottom: 24px;
    text-align: center;
  }
}
.form-field-1-3 {
  width: 33.33%;
}
.form-checkbox {
  line-height: 1;
  margin-top: 15px;
  padding-right: 30px;
  position: relative;
  & input[type="checkbox"] {
    position: absolute;
    clip: rect(0,0,0,0);
    pointer-events: none;
  }
  &--bordered {
    align-self: flex-start;
    border: 1px solid @brand-primary;
    display: inline-block;
    font-size: 17px;
    line-height: 1.77;
    margin: 0 30px 25px;
    padding: 10px 45px 10px 15px;
    & .styled-checkbox {
      right: 15px;
    }
  }
  &--left {
    padding-right: 0;
    padding-left: 30px;
  }
}
.form-checkbox--register {
  font-size: 13px;
  margin: auto;
  max-width: 160px;
}
.styled-checkbox {
  background-color: #fff;
  border: 3px solid #fff;
  cursor: pointer;
  height: 19px;
  margin: 1px;
  outline: 1px solid @brand-primary;
  position: absolute;
  width: 19px;
  top: calc(~'50% - 10px');
  right: 1px;
  input[type="checkbox"]:focus + & {
    outline: 2px solid fade(@brand-primary, 50%);
  }
  input[type="checkbox"]:checked + & {
    background-color: @brand-attention;
  }
  &--left {
    right: auto;
    left: 0;
  }
}
.form-radio {
  padding-left: 25px;
  position: relative;
  & input[type="radio"] {
    position: absolute;
    clip: rect(0,0,0,0);
    pointer-events: none;
  }
}
.styled-radio {
  background-color: #fff;
  border: 2px solid @brand-primary;
  cursor: pointer;
  height: 20px;
  position: absolute;
  width: 20px;
  top: calc(~'50% - 10px');
  left: 0;
  &:after {
    content: '';
    background-image: data-uri('../img/icon-checked.png');
    background-position: 0;
    background-repeat: no-repeat;
    position: absolute;
    top: -6px;
    left: -2px;
    width: 24px;
    height: 23px;
    opacity: 0;
    transition: opacity .3s;
  }
  input[type="radio"]:checked + &:after {
    opacity: 1;
  }
}
.profile-picture-field {
  .clearfix();
}
.profile-picture {
  float: left;
  margin-right: 15px;
  &__img {
    border: 2px solid @brand-primary;
    vertical-align: top;
  }
  &__label {
    font-size: 13px;
    text-align: center;
    & .btn {
      margin-top: 10px;
    }
  }
}

.form-group-flex-all {
  display: flex;
  margin-bottom: 10px;
  & .form-field {
    flex: 1 1 0%;
    margin-bottom: 0;
  }
  & .form-field + .form-field {
    margin-left: 5px;
  }
}

// Form Group Flexbox
@media @tablet {
  .form-field-inline-flex {
    align-items: center;
    display: inline-flex;
    margin-right: 25px;
    margin-bottom: 10px;
    &:last-child {
      margin-right: 0;
    }
    & label {
      flex: 1 0 auto;
      padding-right: 10px;
    }
  }
  .form-group-flex {
    display: flex;
    align-items: center;
    flex: 1 1 auto;
    margin-bottom: 10px;
    & > .form-group {
      flex: 1 1 auto;
    }
    & .form-field {
      flex: 1 1 0%;
      margin-bottom: 0;
      &--button {
        flex: 0 0 auto;
      }
    }
    & .form-field--time {
      width: 130px;
    }
    & .form-field + .form-field {
      margin-left: 5px;
    }
    &--between {
      justify-content: space-between;
    }
  }
}
.form-group-inline {
  // margin-bottom: 10px;
  & .form-field {
    display: inline-block;
    vertical-align: top;
    margin-right: 10px;
    // margin-bottom: 0;
    &:last-child {
      margin-right: 0;
    }
    & .form-control,
    & .ui-selectmenu-button {
      width: 240px !important;
      max-width: 100%;
    }
  }
}

// Form Section
.form-section {
  margin-bottom: 50px;
  &--gray {
    background-color: @gray;
    padding: 15px 25px;
  }
  &__title {
    margin-bottom: 15px;
    text-transform: uppercase;
    &--big {
      font-size: 27px;
    }
    &--middle {
      font-size: 20px;
    }
  }
  &__description {
    font-size: 14px;
    margin: 10px 0 15px;
  }
}

// Buttons
button:focus {
  outline: none;
}
.btn {
  background-color: transparent;
  border: 0;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  line-height: 1.25;
  padding: 0;
  text-align: center;
  text-decoration: none;
  transition: all .3s;
}
.btn-full {
  text-align: center;
  width: 100%;
}
.btn-primary {
  background-color: @brand-primary;
  border-radius: 5px;
  color: #fff;
  padding: 10px 20px;
  text-transform: uppercase;
  &:hover,
  &:focus {
    background-color: @brand-secondary;
    color: #fff;
  }
}
.btn-primary-round {
  background-color: @brand-primary;
  border-radius: 15px;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
  padding: 4px 15px;
  &:hover,
  &:focus {
    background-color: @brand-secondary;
    color: #fff;
  }
}
.btn-notice {
  background-color: @brand-attention;
  border-radius: 5px;
  color: @brand-primary;
  padding: 10px 20px;
  text-transform: uppercase;
  &:hover,
  &:focus {
    background-color: #d78400;
    color: #fff;
  }
}
.btn-notice-round {
  background-color: @brand-attention;
  border-radius: 15px;
  color: @brand-primary;
  font-size: 14px;
  line-height: 20px;
  padding: 4px 15px;
  &:hover,
  &:focus {
    background-color: #d78400;
    color: #fff;
  }
}
.btn-gray {
  background-color: #bebebe;
  border-radius: 5px;
  color: @brand-primary;
  padding: 10px 20px;
  text-transform: uppercase;
  &:hover,
  &:focus {
    background-color: @dark-gray;
    color: #fff;
  }
}
.btn-light-gray {
  background-color: #e4e4e4;
  border-radius: 15px;
  color: #000;
  font-size: 11px;
  line-height: 16px;
  padding: 4px 15px;
  &:hover,
  &:focus {
    background-color: darken(#e4e4e4, 10%);
  }
}
.btn-green-arrow {
  background-color: @brand-green;
  color: #fff;
  font-size: 13px;
  line-height: 1.54;
  margin-left: 15px;
  padding: 6px 10px;
  position: relative;
  text-transform: uppercase;
  &::before {
    content: '';
    border-right: 15px solid #00ab10;
    border-top: 16px solid transparent;
    border-bottom: 16px solid transparent;
    position: absolute;
    top: 0;
    right: 100%;
  }
}
.btn-large {
  font-size: 21px;  
  min-width: 150px;
  max-width: 100%;
  text-align: center;
}
.btn-small {
  font-size: 12px;
  line-height: 1.67;
  padding: 6px 12px;
}
.btn-link {
  display: inline;
  padding: 0;
  &:hover,
  &:focus {
    border-bottom: 1px solid;
  }
}
.ui-dialog-titlebar-close,
.btn-close {
  background-color: @light-gray;
  border: 2px solid #8b8b8b;
  border-radius: 50%;
  display: inline-block;
  padding: 0;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  position: relative;
  &:hover,
  &:focus {
    background-color: #8b8b8b;
  }
}
.ui-dialog-titlebar-close {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 10px;
  border-width: 3px;
  width: 26px;
  height: 26px;
  position: absolute;
  top: -12px;
  right: -12px;
  z-index: 102;
}
.btn-close-icon {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  width: 8px;
  height: 8px;
  position: absolute;
  top: calc(~'50% - 4px');
  left: calc(~'50% - 4px');
}
.ui-dialog-titlebar-close,
.btn-close-icon {
  background-image: data-uri('../img/icon-close-gray.svg');
}
.ui-dialog-titlebar-close:hover,
.ui-dialog-titlebar-close:focus,
.btn-close:hover .btn-close-icon,
.btn-close:focus .btn-close-icon {
  background-image: data-uri('../img/icon-close-white.svg');
}

// Comments
.comment-control {
  border: 3px solid #eaeaea;
  font-size: 14px;
  padding: 5px 20px;
  width: 100%;
  transition: box-shadow .3s, border-color 0.3s ease-in-out;
  input[type="text"]& {
    height: 34px;
  }
  textarea& {
    padding-top: 10px;
    padding-bottom: 10px;
    height: 132px;
  }
  &:focus {
    border-color: #bebebe;
    box-shadow: 0 0 5px #cacaca;
    outline: none;
  }
}
.btn-comment {
  font-size: 14px;
  height: 35px;
  padding: 5px 20px;
}

// Checkout
.returning-customer {
  margin-bottom: 50px;
  @media @tablet-landscape {
    display: flex;
  }
  &__title {
    align-self: center;
    font-size: 28px;
    padding: 0 25px;
    text-transform: uppercase;
  }
  &__section {
    background-color: @brand-attention;
    flex: 1 1 0%;
    margin-top: 10px;
    padding: 10px 25px;
    position: relative;
    @media @tablet-landscape {
      margin: 0 0 0 20px;
      &::before {
        content: '';
        border-right: 20px solid @brand-attention;
        border-top: 15px solid transparent;
        border-bottom: 15px solid transparent;
        position: absolute;
        top: calc(~'50% - 15px');
        right: 100%;
      }
    }
  }
  &__section-label {
    font-size: 16px;
  }
}
.checkout-tail {
  padding: 0 10px;
}
.checkout-total {
  margin-bottom: 40px;
  &__title {
    font-size: 22px;
  }
  &__note {
    font-size: 13px;
    text-align: right;
  }
}