.ui-dialog {
  background-color: #fff;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.4);
  outline: none;
  position: absolute;
  max-width: calc(~'100vw - 24px');
  top: 0;
  left: 0;
  z-index: 101;
  &--gray{
    background-color: #e0e2e6;
  }
  @media @max-tablet {
    width: calc(~'100vw - 24px') !important;
    left: 12px !important;
  }
}
.hasArrow {
  &::before {
    content: '';
    border-bottom: 17px solid #e0e2e6;
    border-right: 12px solid transparent;
    border-left: 12px solid transparent;
    position: absolute;
    bottom: 100%;
    left: 40px;
    @media @max-tablet {
      display: none;
    }
  }
}
.ui-dialog .ui-dialog-titlebar {
  // padding: .4em 1em;
  // position: relative;
}
.ui-dialog .ui-dialog-title {
  // float: left;
  // margin: .1em 0;
  // width: 90%;
  display: none;
  overflow: hidden;
  padding: 0 30px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.ui-button-icon-only .ui-button-text {
  .sr-only();
}
.ui-dialog-content {
  position: relative;
  padding: 25px 30px;
  overflow: auto;
}
.ui-dialog .ui-dialog-buttonpane {
  text-align: left;
  border-width: 1px 0 0 0;
  background-image: none;
  margin-top: .5em;
  padding: .3em 1em .5em .4em;
}
.ui-dialog .ui-dialog-buttonpane .ui-dialog-buttonset {
  float: right;
}
.ui-dialog .ui-dialog-buttonpane button {
  margin: .5em .4em .5em 0;
  cursor: pointer;
}
.ui-dialog .ui-resizable-n {
  height: 2px;
  top: 0;
}
.ui-dialog .ui-resizable-e {
  width: 2px;
  right: 0;
}
.ui-dialog .ui-resizable-s {
  height: 2px;
  bottom: 0;
}
.ui-dialog .ui-resizable-w {
  width: 2px;
  left: 0;
}
.ui-dialog .ui-resizable-se,
.ui-dialog .ui-resizable-sw,
.ui-dialog .ui-resizable-ne,
.ui-dialog .ui-resizable-nw {
  width: 7px;
  height: 7px;
}
.ui-dialog .ui-resizable-se {
  right: 0;
  bottom: 0;
}
.ui-dialog .ui-resizable-sw {
  left: 0;
  bottom: 0;
}
.ui-dialog .ui-resizable-ne {
  right: 0;
  top: 0;
}
.ui-dialog .ui-resizable-nw {
  left: 0;
  top: 0;
}
.ui-draggable .ui-dialog-titlebar {
  cursor: move;
}

/* Overlays */
.ui-widget-overlay {
  background-color: rgba(0,0,0,.3s);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

//
.dialog {
  display: none;
  &__title {
    font-size: 15px;
    line-height: 18px;
    margin-bottom: 10px;
  }
  &__description {
    font-size: 12px;
    margin-bottom: 15px;
  }
  &__note {
    background-color: #f2f2f2;
    font-size: 12px;
    margin: 0 -10px 10px;
    padding: 10px;
  }
  &__note-title {
    font-size: 18px;
    margin-bottom: 10px;
    text-align: center;
    text-transform: uppercase;
    &--error {
      color: #e00000;
    }
  }
  &__footer {
    border-top: 2px solid #fff;
    margin-top: 20px;
    padding-top: 10px;
    text-align: center;
  }
  &__separator {
    display: inline-block;
    margin: 0 5px;
    &:before {
      content: '|';
    }
  }
}

.travel-agent-dialog {
  background: 0 100% no-repeat url(../img/travel-agent-dialog-bg.png);
  padding-bottom: 61px;
}

@media @tablet {
  .dialog__column {
    width: 238px;
    flex: 0 0 auto;
  }
}
.dialog__column-info {
  padding-top: 25px;
}

.dialog-img-wrapper {
  margin-bottom: 8px;
  text-align: center;
  & img {
    vertical-align: top;
  }
}

// Message Dialog Styles
.dialog-message {
  color: @brand-primary;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}
.dialog-buttons {
  margin-top: 30px;
  text-align: center;
}
.message-img {
  display: block;
  margin: 20px auto;
  vertical-align: middle;
  @media @tablet {
    display: inline-block;
    margin: 0 80px;
  }
}

@media @max-tablet-landscape {
  .ui-travel-agent-dialog {
    width: 550px !important;
  }
  .travel-agent-row {
    display: block;
  }
  .dialog__column-info {
    width: 100%;
  }
}