.destination-wrapper {
  margin-top: 30px;
}
.destination-tabs-nav {
  background-color: @brand-secondary;
  display: flex;
  list-style: none;
  margin: 0 -20px;
  padding: 0 5px;
  & li {
    border-right: 3px solid #fff;
    flex: 0 1 auto;
    outline: none;
    position: relative;
    &:hover,
    &:focus {
      background-color: @brand-green;
    }
  }
  & a {
    color: #fff;
    display: block;
    font-size: 12px;
    outline: none;
    padding: 11px 5px;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
  }
  & .ui-state-active {
    background-color: @brand-green;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 100%;
      right: 0;
      left: 0;
      height: 18px;
      background: no-repeat 0 100% data-uri('../img/icon-triangle.svg');
      background-size: 100% 18px;
    }
  }
  & li.severe {
    background-color: @severe;
    &::after {
      background-image: data-uri('../img/icon-triangle-severe.svg');
    }
  }
  &__tooltip {
    background-color: #fff;
    box-shadow: 0 0 3px rgba(0,0,0,0.5);
    color: @severe;
    font-size: 15px;
    margin-bottom: 7px;
    padding: 9px 20px;
    position: absolute;
    bottom: 100%;
    right: 0;
    text-transform: none;
    white-space: nowrap;
    z-index: 1;
    &::after {
      content: '';
      border-top: 14px solid #fff;
      border-right: 13px solid transparent;
      border-left: 13px solid transparent;
      filter: drop-shadow(0 1px rgba(0, 0, 0, 0.2) );
      position: absolute;
      top: 100%;
      right: 20px;
    }
  }
}

.destination-tabs {
  padding-top: 40px;
}
.destination-tab:not(:first-child) {
	display: none;
}
.destination-tab__title {
  color: @brand-primary;
  font-size: 20px;
  line-height: 1;
  font-style: oblique;
  margin: 0 0 10px;
  text-transform: uppercase;
}
.destination-tab__main {
  margin-bottom: 20px;
}
.destination-tab__aside {
  display: flex;
  flex-direction: column;
}

// Destination Gallery
.destination-gallery {
  & .swiper-slide__img {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 370px;
  }
  & .swiper-button-white {
    top: 195px;
  }
} 
.destination-thumbs {
  display: flex;
  flex: 0 0 auto;
  height: 110px;
  margin: 0 -10px;
  .destination-thumb {
    background-clip: content-box;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    padding: 0 10px;
    width: 33%;
  }
}

.destination-map {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  margin-top: 25px;
  min-height: 400px;
}
#map {
  flex: 1 1 auto;
  min-height: 200px;
  width: 100%;
}

.destination-offer {
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  display: block;
  flex: 0 0 auto;
  margin-top: 20px;
  max-width: 379px;
  min-height: 125px;
  padding: 7px 40px 0 125px;
  text-decoration: none;
  text-shadow: 0 3px 3px rgba(0, 0, 0, 0.75);
  transition: all .3s;
  &:hover,
  &:focus {
    text-shadow: 0 3px 3px rgba(255, 255, 255, 0.75);
  }
  &__title {
    font-size: 30px;
    font-weight: 700;
    line-height: 1;
    margin: 0;
    text-transform: uppercase;
  }
  &__content {
    align-items: center;
    display: flex;
    padding: 0 0 4px;
  }
  &__price-label,
  &__price-tail {
    font-size: 24px;
    line-height: 1;
    text-transform: uppercase;
  }
  &__price-value {
    font-size: 57px;
    line-height: 1;
  }
  &__footer {
    font-size: 17px;
    font-weight: 400;
    line-height: 1;
    margin: 0;
    white-space: nowrap;
  }
}
@media @mobile {
  .destination-tabs-nav {
    padding: 0 20px;
    a {
      font-size: 16px;
      padding: 11px;
    }
  }
}
@media @tablet-landscape {
  .destination-tabs-nav__tooltip {
    right: auto;
    left: 0;
    &::after {
      right: auto;
      left: 20px;
    }
  }
  .destination-tab__content {
    display: flex;
  }
  .destination-tab__main {
    margin-bottom: 0;
    width: 62%;
  }
  .destination-tab__aside {
    width: 38%;
    padding-left: 20px;
  }
  .destination-map {
    min-height: 235px;
  }
  .destination-tabs-nav {
    a {
      padding: 11px 30px;
    }
  }
}


// Tours Grid
.tours-grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
  &__item {
    background-color: @gray;
    color: @brand-primary;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    flex: 0 1 auto;
    margin: 0 1% 20px;
    padding: 0 15px 15px;
    position: relative;
    text-decoration: none;
    @media @mobile {
      width: 48%;
    }
    @media @tablet {
      width: 31.33%;
    }
    &:hover,
    &.selected {
      background-color: #fff;
      box-shadow: 0 0 0 4px @selected-border-color inset;
    }
  }
  &__item-header {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 215px;
    position: relative;
    margin: 0 -15px;
    .tours-grid__item:hover &::after,
    .selected &::after {
      content: '';
      border-color: @selected-border-color;
      border-style: solid;
      border-width: 4px 4px 0;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      pointer-events: none;
    }
  }
  &__item-price {
    background-color: rgba(0,0,0,0.6);
    color: #fff;
    font-size: 22px;
    line-height: 1;
    padding: 10px;
    position: absolute;
    bottom: 20px;
    left: 0;
    &--right {
      right: 0;
      left: auto;
    }
  }
  &__item-price-label {
    font-size: 16px;
  }
  &__item-title {
    margin: 10px 0 5px;
    text-align: center;
    text-transform: uppercase;
  }
  &__item-description {
    flex: 1 1 auto;
    font-size: 14px;
    margin-bottom: 20px;
  }
  &__item-footer {
    text-align: center;
  }
  &__item-header + &__item-footer {
    margin-top: 10px;
  }
  &__item-note {
    font-size: 12px;
    line-height: 1.34;
    margin-top: 15px;
  }
}
.tours-grid__item:hover .btn-notice,
.tours-grid__item.selected .btn-notice {
  background-color: #d78400;
  color: #fff;
}
.tour-offer {
  width: 100px;
  height: 100px;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  &__body {
    background-color: rgba(232,0,0,.7);
    border-radius: 50%;
    color: #fff;
    font-size: 30px;
    line-height: 1;
    width: 142px;
    height: 142px;
    padding: 52px 0 0 52px;
    position: absolute;
    top: -42px;
    left: -42px;
  }
  &__value {
    font-size: 42px;
    line-height: 1;
  }
}

.desctination-label {
  color: @brand-primary;
  font-size: 18px;
  font-style: oblique;
  font-weight: bold;
  line-height: 1;
  margin: 20px 0 30px;
}
.destination-section {
  background-color: @gray;
  color: @brand-primary;
  margin-bottom: 30px;
  padding: 15px 25px 5px;
}
.destination-date {
  font-size: 18px;
  margin-bottom: 10px;
  &__label {
    font-weight: 600;
  }
  &__content {
    white-space: nowrap;
  }
}
.form-destination-direction {
  margin-left: -10px;
}
.form-field-destination {
  padding-left: 0;
  &__label {
    border-radius: 20px;
    font-size: 15px;
    line-height: 1.67;
    padding: 6px 10px 6px 35px;
    text-transform: uppercase;
    transition: background-color .3s;
  }
  input[type="radio"]:checked + &__label {
    background-color: #fff;
    & .styled-radio:after {
      opacity: 1;
    }
  }
  .styled-radio {
    left: 10px;
  }
}
.destination-airport {
  font-size: 18px;
  padding-left: 75px;
  &__label {
    float: left;
    font-weight: 600;
    margin-left: -75px;
    width: 75px;
  }
  & a {
    font-weight: 600;
  }
}

// Symbology Reference
.symbology-reference-title {
  color: @brand-primary;
  font-size: 20px;
  line-height: 1;
  font-style: oblique;
  margin: 0 0 10px;
}
.symbology-reference {
  color: @brand-primary;
  list-style: none;
  margin: 0;
  padding: 0;
  & li {
    margin-bottom: 15px;
    padding: 0 10px 0 30px;
    position: relative;
    & span {
      white-space: nowrap;
    }
    & span[class^="icon"] {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}
.icon-triangle {
  border-bottom: 20px solid @brand-primary;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  display: inline-block;
  vertical-align: middle;
  &.small {
    border-width: 0 6px 12px;
    margin: 0 0 0 5px;
  }
}
.icon-square {
  color: #fff;
  display: inline-block;
  height: 20px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  width: 20px;
  &.green {
    background-color: @brand-green;
  }
  &.orange {
    background-color: #ff9300;
  }
  &.yellow {
    background-color: #ffe000;
  }
  &.red {
    background-color: #f00;
  }
  &.small {
    width: 10px;
    height: 10px;
    margin-right: 5px;
  }
}
.icon-plane {
  background: no-repeat data-uri('../img/icon-plane.png');
  background-size: contain;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
}
.icon-setup-alert {
  background: no-repeat data-uri('../img/icon-setup-alert.png');
  background-size: contain;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 25px;
}
@media @mobile {
  .symbology-reference {
    display: flex;
    flex-wrap: wrap;
    & li {
      flex: 0 1 auto;
      width: 50%;
    }
  }
}
@media @tablet {
  .symbology-reference {
    & li {
      width: 25%;
    }
  }
}
@media @tablet-landscape {
  .symbology-reference {
    & li {
      width: 20%;
    }
  }
}