.promo-code-form {

}
.cart-item {
  background-color: @light-gray;
  color: @brand-primary;
  margin-bottom: 20px;
  padding: 15px 20px;
  &__header {
    background-color: #e9e9e9;
    margin: 0 -20px 10px;
    padding: 5px 20px;
    @media @mobile {
      display: flex;      
    }
  }
  &__header-title {
    flex: 1 1 auto;
    font-size: 24px;
    font-weight: 700;
    padding-right: 15px;
  }
  &__subtotal {
    flex: 0 0 auto;
    font-size: 26px;
    line-height: 1.15;
    padding-left: 15px;
    text-align: right;
  }
  &__date {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  &__quantity,
  &__services,
  &__supplement {
    margin-bottom: 10px;
  }  
  &__supplement-title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 5px;
  }
}
@media @tablet-landscape {
  .cart-actions {
    display: flex;
  }
  .cart-do-actions {
    flex: 1 1 auto;
    text-align: right;
  }
}
.cart-total {
  background-color: @light-gray;
  border: 3px solid #e9e9e9;
  color: @brand-primary;
  padding: 15px;
  text-align: center;
  &__promotion {
    font-size: 11px;
    margin-bottom: 10px;
  }
  &__promotion-prefix {
    display: inline-block;
  }
  &__promotion-label {
    color: @brand-green;
    display: inline-block;
  }
  &__saving {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  &__saving-label {
    color: @brand-green;
  }
  &__label {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  &__amount {
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 30px;
  }
}

.promo-code-form {
  margin-top: 10px;
  @media @tablet {
    margin: 0 auto;
    & .form-group-flex {
      margin-bottom: 0;
    }
  }
  & .form-control {
    background-color: @gray;
  }
}