.faqs-container {
  position: relative;
}
.faqs-control {
  border: 1px solid @brand-primary;
  color: #000;
  font-size: 18px;
  line-height: 30px;
  padding: 8px 15px;
  width: 100%;
  height: 46px;
  &:focus {
    border-color: fade(@brand-primary, 50%);
    outline: 1px solid fade(@brand-primary, 50%);
  }
  .placeholder(#939393);
}
.faqs-aside {
  display: none;
}
.faqs-tabs-nav {
  background-color: #efead9;
  list-style: none;
  margin: 0;
  padding: 20px 0;
  & a {
    color: @brand-primary;
    display: block;
    margin: 1px 0;
    outline: none;
    padding: 4px 35px;
    position: relative;
    text-decoration: none;
    transition: all .3s;
    &:hover {
      background-color: #cbbd8f;
      color: #fff;
    }
  }
  .ui-state-active a {
    background-color: #cbbd8f;
    color: #fff;
    position: relative;
    &::after {
      content: '';
      border-left: 20px solid #cbbd8f;
      border-top: 19px solid transparent;
      border-bottom: 19px solid transparent;
      position: absolute;
      top: calc(~'50% - 19px');
      left: 100%;
    }
  }
  &__content {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.faqs-nav-filter {
  color: @brand-primary;
  font-size: 15px;
  font-weight: 600;
  padding: 4px 35px;
  &__title {
    border-bottom: 1px solid;
  }
}
.faqs-nav-separator {
  border-top: 20px solid #efead9;
  border-bottom: 20px solid #efead9;
  background-color: #fff;
  height: 65px;
}
.faqs-tabs-content {
  border-left: 3px solid #cbbd8f;
  margin-top: 20px;
  position: relative;
}
.faqs-question {
  background-color: #efead9;
  margin-bottom: 15px;
  padding: 10px 15px;
  position: relative;
}
.faqs-filter {
  font-size: 24px;
  padding: 8px 20px;
}
.faqs-header {
  background-color: #cbbd8f;
  color: #fff;
  font-size: 24px;
  margin-bottom: 1px;
  overflow: hidden;
  padding: 8px 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.faqs-question-title {
  color: @brand-primary;
  font-weight: 600;
  margin: 0 0 10px;
  padding-right: 15px;
  &--closed {
    cursor: pointer;
    margin-bottom: 0;
  }
}
.question-close-button {
  background: no-repeat 50% data-uri('../img/icon-close-faqs.svg');
  border: 0;
  display: inline-block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
}

@media @max-tablet {
  .faqs-panel {
    display: block !important;
  }
}

@media @tablet {  
  .faqs-search {
    position: absolute;  
    width: 280px;
    position: absolute;
    top: 0;
    right: 0;
  }
  .faqs-aside {
    display: block;
    padding: 63px 20px 0 0;
  }
  .faqs-tabs-content {
    margin-top: 63px;
  }
  .faqs-filter {
    display: none;
  }
  .faqs-header {
    max-width: calc(~'100% - 283px');
    position: absolute;
    top: -63px;
    left: 0;
  }
  .faqs-panel:not(:first-child) {
    display: none;
  }
  .faqs-questions {
    display: block !important;
  }
  .faqs-question {
    margin-bottom: 25px;
    padding: 20px 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .faqs-question-title {
    padding-right: 0;
  }
}
