.ui-tooltip {
  background-color: #fff;
  box-shadow: 0 0 5px @brand-primary;
  color: @brand-primary;
  padding: 8px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  max-width: 300px;
}
.form-tooltip {
  cursor: default;
  display: inline-block;
  text-align: center;
  width: 20px;
  &--extend {
    color: #a7a7a7;
    font-size: 25px;
    font-weight: 700;
    line-height: 1;
    position: absolute;
    bottom: 4px;
    left: 100%;
  }
  @media @tablet-landscape {
    width: 25px;
  }
}
.form-help-tooltip {
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  color: @brand-primary;
  cursor: default;
  display: inline-block;
  flex: 0 0 auto;
  font-family: @alternative-fonts;
  font-size: 20px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
  vertical-align: middle;
  width: 28px;
}
.tooltip-trigger {
  background-color: @light-gray;
  border-radius: 50%;
  border: 2px solid #8b8b8b;
  color: #8b8b8b;
  cursor: default;
  display: inline-block;
  font-size: 14px;
  height: 20px;
  line-height: 16px;
  text-align: center;
  vertical-align: middle;
  width: 20px;
}
