.tour-gallery-section {
  background-color: @gray;
  padding: 20px;
  margin-bottom: 20px;
}
.tour-gallery-thumbs {
  display: flex;
  flex-wrap: wrap;
}
.tour-gallery-thumb {
  background-clip: content-box;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 1 1 auto;
  height: 165px;
  padding: 10px;
  width: 50%;
}
.tour-gallery-main {
  background-clip: content-box;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10px;
  min-height: 330px;
}
.page-block {
  margin-bottom: 40px;
}
.tour-price {
  display: flex;
  font-size: 15px;
  &__criteria {
    flex: 1 1 auto;
  }
  &__criteria-item {
    display: inline-block;
    vertical-align: top;
  }
  &__start {
    font-weight: 700;
  }
}
.tour-details {
  margin: 10px 0 20px;
}
.tour-provider {
  flex: 1 1 auto;
  font-size: 15px;
  padding-top: 15px;
  & h4 {
    display: inline-block;
    font-size: 15px;
    font-weight: 700;
    margin: 0;
  }
}
.tour-video {
  box-shadow: 0 0 10px rgba(0,0,0,.4);
  max-width: 640px;
  margin: auto;
}
.tour-aside {
  min-width: 300px;
}
.aside-heading {
  background: @light-gray no-repeat 15px 50% data-uri('../img/icon-arrow.png');
  font-size: 20px;
  text-transform: uppercase;
  margin: 0 -15px 10px;
  padding: 5px 15px 5px 35px;
}
.aside-section {
  font-size: 14px;
  margin: 0 0 20px 20px;
}

.tour-availability {
  background-color: @brand-secondary;
  color: #fff;
  margin-bottom: 20px;
  padding: 20px;
  &__header {
    font-size: 18px;
    line-height: 1;
    margin-bottom: 15px;
    text-transform: uppercase;
  }
  & label {
    color: #fff;
    font-size: 12px;
    & strong {
      display: block;
      font-size: 15px;
      font-weight: 400;
      text-align: center;
    }
  }
  &__title {
    font-size: 15px;
    margin: 10px 0 5px;
  }
  &__note {
    font-size: 12px;
  }
  &__supplements {
    font-size: 15px;
    line-height: 1.34;
    margin-bottom: 5px;
    text-align: right;
  }
  &__supplements-label {
    float: left;
    font-size: 12px;
    line-height: 1.67;
  }
  &__total {
    font-size: 18px;
    line-height: 1.11;
    margin-bottom: 20px;
    text-align: right;
    & .tour-availability__note {
      margin-top: 5px;
    }
  }
  &__total-label {
    float: left;
    font-size: 15px;
    line-height: 1.34;
  }
}

@media @tablet {
  .tour-gallery-section {
    display: flex;
  }
  .tour-gallery-thumbs {
    flex: 1 1 auto;
    width: 47.5%;
  }
  .tour-gallery-main {
    flex: 1 1 auto;
    padding: 0 0 0 10px;
    width: 52.5%;
  }
  .tour-details {
    display: flex;
    & .week-days {
      flex: 0 0 auto;
    }
  }
  .tour-aside {
    padding-left: 15px;
  }
}
@media @tablet-landscape {
  .tour-availability {
    padding: 20px 30px;
  }
}