.page-header__top-section {
  max-width: @section-max-width;
  margin: auto;
  padding: 7px 20px 50px;
  position: relative;
  @media @tablet {
    padding-bottom: 1px;
  }
}
.page-header__row {
  &:after {
    content: '';
    clear: both;
    display: block;
  }
  &--logo {
    text-align: center;
  }
}
.page-top {
  margin-bottom: 10px;
}

.site-search {
  position: absolute;
  right: 75px;
  bottom: 10px;
  left: 20px;
  &__input {
    border: 2px solid #00558a;
    color: #001327;
    font-size: 16px;
    width: 100%;
    height: 40px;
    padding: 5px 32px 5px 10px;
    .placeholder(#001327);
    &:focus {
      outline: none;
      box-shadow: 0 0 3px #00558a inset;
    }
  }
  &__button {
    background: data-uri('../img/search.png');
    border: 0;
    width: 22px;
    height: 22px;
    padding: 0;
    position: absolute;
    top: calc(~'50% - 11px');
    right: 8px;
  }
  @media @tablet {
    float: right;
    margin-left: 5px;
    position: relative;
    right: auto;
    bottom: auto;
    left: auto;
    width: 250px;
    &__input {
      font-size: 14px;
      height: 32px;
    }
    &__button {
      right: 5px;
    }
  }
}

.shopping-cart-action {
  color: #014b8d;
  float: right;
  padding: 5px;
  text-decoration: none;
  transition: all .3s;
  &:hover {
    background-color: #014b8d;
    color: #fff;
  }
  &__value {
    display: inline-block;
    vertical-align: middle;
    font-size: 14px;
    line-height: 20px;
    margin: 0 5px;
  }
  &__value:before {
    content: '';
    display: inline-block;
    vertical-align: text-bottom;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 8px solid #00558a;
    margin-right: 4px;
    transition: all .3s;
    .shopping-cart-action:hover & {
      border-left-color: #fff;
    }
  }
}
.cart-icon {
  display: inline-block;
  vertical-align: middle;
  fill: #00558a;
  width: 21px;
  height: 19px;
  transition: all .3s;
  .shopping-cart-action:hover & {
    fill: #fff;
  }
}

// User Actions
.user-actions {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 6px 0 0;
}
.user-action-item {
  // display: inline-block;
  // vertical-align: top;
  flex: 1 1 auto;
  position: relative;
}
.user-action {  
  border-bottom: 2px solid transparent;
  color: #07284c;
  display: inline-block;
  font-family: @alternative-fonts;
  font-size: 14px;
  padding: 3px;
  // margin: 0 30px;
  text-decoration: none;
  transition: border-color .3s;
  // .user-action-item:first-child & {
  //   margin-left: 0;
  // }
  &:hover,
  &:focus {
    border-color: #07284c;
  }
}

.site-logo-wrapper {
  display: inline-block;
  padding: 10px;
}

.top-contacts {
  &--header {
    margin-top: 10px;
  }
  & a {
    color: inherit;
    text-decoration: none;
  }
  @media @tablet {
    float: right;
    text-align: right;
  }
}
.top-contacts__item {
  color: #07284c;
  font-size: 14px;
}
