.jqueryui-selectmenu,
.jqueryui-gray-selectmenu,
.jqueryui-brand-selectmenu {
  opacity: 0;
}
.ui-selectmenu-menu {
  padding: 0;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 102;
  display: none;
}
.ui-selectmenu-menu .ui-menu {
  border: 1px solid #999;
  border-top: 0;
  background-color: #fff;
  font-size: 15px;
  line-height: 20px;
  list-style: none;
  margin: 0;
  overflow: auto;
  overflow-x: hidden;  
  padding: 0;
  max-height: 260px;
  &--gray {
    background-color: @gray;
    border: 0;
  }
  &--brand {
    background-color: @brand-primary;
    border: 0;
    color: #fff;
  }
}
.ui-selectmenu-menu .ui-menu .ui-selectmenu-optgroup {
  font-size: 1em;
  font-weight: bold;
  line-height: 1.5;
  padding: 2px 0.4em;
  margin: 0.5em 0 0 0;
  height: auto;
  border: 0;
}
.ui-selectmenu-menu .ui-menu-item {
  cursor: pointer;
  padding: 3px 10px;
  transition: background-color .3s;
  &:hover {
    background-color: #f4f4f4;
    color: @text;
  }
  &:first-child {
    padding-top: 6px;
  }
  &:last-child {
    padding-bottom: 6px;
  }
}
.ui-selectmenu-menu .ui-state-disabled {
  display: none;
}
.ui-selectmenu-open {
  display: block;
}
.ui-selectmenu-button {
  background-color: #fff;
  border: 1px solid #d8d8d8;
  display: inline-block;
  overflow: hidden;
  position: relative;
  text-decoration: none;
  cursor: pointer;
  vertical-align: top;
  width: 100% !important;
  &:focus {    
    border-color: fade(@brand-primary, 50%);
    outline: 1px solid fade(@brand-primary, 50%);
  }
  .form-control--searchbox + & {
    background-color: #f4f4f4;
    border-color: @brand-primary;
  }
  & span.ui-icon {  
    background-color: #07284c;
    height: 32px;
    width: 32px;
    position: absolute;
    right: 0;
    top: 0;
    &:after {
      content: '';
      border-top: 11px solid #fff;
      border-right: 7px solid transparent;
      border-left: 7px solid transparent;
      position: absolute;
      right: 8px;
      top: 10px;
    }
  }
  & span.ui-selectmenu-text {
    color: #000;
    font-size: 15px;
    line-height: 20px;
    padding: 5px 35px 5px 10px;
    text-align: left;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &--gray {
    border-color: @gray;
    background-color: @gray;
    & span.ui-icon {
      background-color: transparent;
      &:after {
        border-top-color: @brand-primary;
      }
    }
    & span.ui-selectmenu-text {
      text-transform: none;
    }
  }
  &--brand {
    border-color: @brand-primary;
    background-color: @brand-primary;
    & span.ui-icon {
      background-color: transparent;
      &:after {
        border-top-color: #fff;
      }
    }
    & span.ui-selectmenu-text {
      color: #fff;
      text-transform: none;
    }
  }
}
