.menu-trigger {
  background-color: @gray;
  border: 1px solid @brand-primary;
  width: 45px;
  height: 40px;
  padding: 0;
  position: absolute;
  right: 20px;
  top: -50px;
  &__bar {
    background-color: @brand-primary;
    height: 4px;
    position: absolute;
    top: 10px;
    right: 10px;
    left: 10px;
    &::before,
    &::after {
      content: '';
      background-color: @brand-primary;
      width: 100%;
      height: 4px;
      position: absolute;
      top: 7px;
      left: 0;
    }
    &::after {
      top: 14px;
    }
  }
  &:hover,
  &:focus {
    background-color: @brand-primary;
  }
  &:hover &__bar,
  &:focus &__bar {
    background-color: @gray;
    &::before,
    &::after {
      background-color: @gray;
    }
  }
  @media @tablet {
    display: none;
  }
}

.top-nav-box {
  position: relative;
  @media @tablet {
    display: block !important;
    margin-top: 10px;
  }
}
.top-nav {
  background-color: @brand-primary;
  list-style: none;
  padding: 0;
  margin: auto;
  max-width: 1090px;
  display: none;
  &--opened {
    display: block;
  }
  &__item {
    flex: 1 1 auto;
  }
  &__item-title {
    border-bottom: 1px solid @brand-secondary;
    color: #fff;
    cursor: pointer;
    display: block;
    font-size: 18px;
    line-height: 20px;
    padding: 13px 13px 11px 50px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    transition: color .3s, border-color .3s;
    &:hover,
    &:focus {
      color: #fff;
    }
  }
}

@media @max-tablet {
  .top-nav {
    &__item {
      &--current,
      &--subview {
        // box-shadow: 10px 0 10px -10px #000;
        margin-right: 45px;
      }
    }
    &__item-title {
      &--submenu {
        &::before {
          content: '';
          background: 50% no-repeat data-uri('../img/icon-arrow-left.svg');
          color: #fff;
          width: 36px;
          height: 36px;
          position: absolute;
          top: calc(~'50% - 18px');
          left: 10px;
        }
      }
      &--current {
        padding-left: 25px;
        &::before {
          display: none;
        }
      }
      &--back {
        background-color: @brand-secondary;
        display: none;
        padding-right: 50px;
        padding-left: 25px;
        &::after {
          content: '';
          background: 50% no-repeat data-uri('../img/icon-arrow-right.svg');
          color: #fff;
          width: 36px;
          height: 36px;
          position: absolute;
          top: calc(~'50% - 18px');
          right: 15px;
        }
      }
    }
  }  
  .top-nav-icon {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: 26px;
    display: none;
    width: 45px;
    height: 45px;
    position: absolute;
    top: 0;
    right: 0;
    .top-nav--first-level > & {
      background-image: data-uri('../img/icon-home.svg');
    }
    .top-nav--second-level > & {
      background-image: data-uri('../img/icon-earth.svg');
      right: 45px;
    }
    .top-nav--subview > & {
      display: block;
    }
  }
  .top-nav--opened .top-nav--current,
  .top-nav--opened .top-nav--subview,
  .top-nav__submenu.top-nav--current > .top-nav__item,
  .top-nav__item--current > .top-nav__item-title--back {
    display: block;
  }
  .top-nav--subview > .top-nav__item:not(.top-nav__item--current):not(.top-nav__item--subview),
  .top-nav__item--subview > .top-nav__item-title {
    display: none;
  }
  .top-nav--first-level.top-nav--subview {
    background-color: #013565;
  }
  .top-nav--second-level.top-nav--subview {
    background-color: #002f59;
  }
}

@media @tablet {
  .top-nav-box {
    background-color: @brand-primary;
  }
  .top-nav {
    background-color: transparent;
    display: block;
  }
  .top-nav--first-level {
    display: flex;
    flex-wrap: wrap;
    & > li > .top-nav__item-title {
      border-bottom: 3px solid @brand-primary;
      padding: 13px 13px 11px;
      text-align: center;
    }
    & > li:hover > .top-nav__item-title,
    & > li > .top-nav__item-title:focus {
      background-color: @brand-secondary;
      border-color: #fd9b00;
      color: #fff;
    }
    & > li.current > .top-nav__item-title {
      background-color: #001327;
      border-color: #fd9b00;
      color: #fff;
    }
  }
  .top-nav--second-level {
    background-color: fade(@brand-primary, 90%);
    position: absolute;
    opacity: 0;
    padding: 35px 20px 15px;
    transition: all .3s ease .3s;
    visibility: hidden;
    z-index: 2;
    li:hover & {
      opacity: 1;
      visibility: visible;
    }
    & > .top-nav__item {
      display: inline-block;
      vertical-align: top;
      width: 130px;
      margin: 0 20px 20px 0;
      &:last-child {
        margin-right: 0;
      }
    }
    & .top-nav__item-title {
      border-bottom: 1px solid #fff;
      color: #ffb400;
      font-size: 15px;
      line-height: 1;
      margin-bottom: 6px;
      padding: 7px;
      text-align: left;
      text-transform: uppercase;
      &:hover,
      &:focus {
        color: #fff;
      }
    }
  }
  .top-nav--third-level {
    & .top-nav__item-title {
      border: 0;
      font-size: 12px;
      line-height: 15px;
      margin-bottom: 0;
      padding: 6px 7px;
    }
  }
  .top-nav__item-title--back {
    display: none;
  }
}
