.confirmation-well {
  border: 3px solid #ccc;
  color: @brand-primary;  
  margin-bottom: 15px;
  max-width: 970px;
  padding: 15px 20px;
  text-align: center;
  &__motto {
    color: @brand-secondary;
    font-size: 12px;
    font-style: oblique;
    margin-bottom: 15px;
  }
  &__title {
    font-size: 26px;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__description {
    font-size: 19px;
    text-transform: uppercase;
    & p {
      margin-bottom: 0;
    }
  }
  &__mail {
    font-size: 19px;
    font-weight: 700;
  }
}
.confirmation-print {
  margin-bottom: 15px;
  text-align: center;
}
.icon-print {
  background: data-uri('../img/icon-print.png');
  display: inline-block;
  vertical-align: middle;
  width: 26px;
  height: 23px;
  margin-right: 10px;
}
.icon-content {
  display: inline-block;
  vertical-align: middle;
}
.confirmation-content {
  color: @brand-primary;
  font-size: 14px;
  line-height: 1.36;
  margin-bottom: 20px;
  padding: 0 20px;
  & h3 {
    font-weight: 600;
    margin-bottom: 10px;
  }
  & p {
    margin: 0;
  }
}
@media @tablet {
  .confirmation-content.flex-col-aside {
    padding-left: 0;
  }
}
.confirmation-label {
  font-weight: 600;
}
.table-confirmation {
  margin-bottom: 25px;
  & th {
    background-color: @brand-secondary;
  }
  & td {
    color: @brand-primary;
    font-size: 16px;
    line-height: 1.25;
  }
  & tr:nth-child(odd) td {
    background-color: transparent;
  }
  & tr:nth-child(even) td {
    background-color: @gray;
  }
  .section-header + & {
    margin-top: -10px;
  }
}
.confirmation-services {
  text-align: left;
  &__title {
    font-weight: 700;
  }
}
