.page-footer {
  background-color: @brand-secondary;
  color: #fff;
  &__row {
    &--dark {
      background-color: @brand-primary;
      font-size: 11px;
      line-height: 14px;
      padding: 10px 0;
    }
  }
  &__row-content {
    max-width: @section-max-width;
    margin: auto;
    &--top {
      .clearfix();
    }
    &--bottom {
      padding: 0 9px;
      text-align: center;
    }
  }
  &__copyright {
    padding: 0 9px;
  }
}

.footer-nav {
  margin: 0;
  padding: 0;
  list-style: none;
  &__item {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 10px;
    &:not(:last-child):after {
      content: '|';
      display: inline-block;
      margin-left: 4px;
      vertical-align: 1px;
    }
  }
  &__item-content {
    color: #fff;
    padding: 0 9px;
    text-decoration: none;
    transition: color .3s;
    &:hover,
    &:focus {
      color: @brand-attention;
    }
    .footer-nav--top & {
      text-transform: uppercase;
    }
  }
}

.footer-destinations {
  padding: 20px 0 0;
  &__list {
    display: inline-block;
    vertical-align: top;
    min-width: 120px;
    margin: 0 0 20px 20px;
  }
  &__list-title {
    border-bottom: 1px solid @brand-attention;
    color: #fff;
    font-size: 17px;
    font-weight: bold;
    margin-bottom: 15px;
    padding: 2px 5px;
    text-transform: uppercase;
    & a {
      color: #fff;
      text-decoration: none;
      transition: color .3s;
      &:hover,
      &:focus {
        color: @brand-attention;
      }
    }
  }
  &__item {
    margin: 0;
  }
  &__item-title {
    color: #fff;
    border-radius: 9px;
    display: inline-block;
    font-size: 13px;
    line-height: 15px;
    padding: 2px 6px;
    text-decoration: none;
    transition: background-color .3s;
    &:hover,
    &:focus {
      background-color: @brand-primary;
      color: #fff;
    }
  }
}
@media @tablet {
  .footer-nav--top {
    float: right;
  }
  .footer-nav__item {
    margin-bottom: 0;
  }
  
}
@media @tablet-landscape {
  .page-footer-middle {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}