// Car Rental Page Styles

.car-rental-banner {
  background-position: 100% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  max-width: @section-max-width;
  height: 420px;
  margin: auto;
}

.car-rental-searchbox {
  position: absolute;
  bottom: 0;
  right: 30px;
  left: 30px;
  &__header {
    background-color: @brand-primary;
    color: #fff;
    font-size: 18px;
    line-height: 22px;
    padding: 6px;
    text-align: center;
    text-transform: uppercase;
  }
  &__main {
    background-color: rgba(255, 255, 255, 0.8);    
    padding: 10px 25px 15px;
  }
}
@media @mobile {
  .car-rental-searchbox {
    width: calc(~'32.33% - 30px');
    min-width: 285px;
    right: auto;
  }
}

// Columns
.car-rental-results {
  padding: 0;
}
.col-title {
  background-color: @gray;
  color: @brand-primary;
  font-size: 25px;
  line-height: 1;
  margin: 30px 0 15px;
  padding: 20px;
  text-transform: uppercase;
}
.car-rental-info {
  background-color: @gray;
  color: @brand-primary;
  padding: 20px;
  &__header {
    font-size: 21px;
    line-height: 1;
    margin: 0 0 30px;
    text-transform: uppercase;
  }
  &__title {
    font-size: 19px;
    line-height: 1;
    font-weight: bold;
    margin: 0 0 10px;
    text-transform: uppercase;
  }
  &__list {
    list-style: none;
    margin: 0 0 40px;
    padding: 0;
  }
  &__list-item {
    margin-bottom: 10px;
  }
}

// Car Item
.car-item {
  background-color: #f1f1f1;
  cursor: pointer;
  margin-right: 1%;
  margin-left: 1%;
  padding: 20px;
  text-align: center;
  margin-bottom: 15px;
  &:hover,
  &.selected {
    box-shadow: 0 0 0 4px @selected-border-color inset;
  }
  &__type {
    color: @brand-primary;
    font-size: 21px;
    line-height: 1;
    margin-bottom: 10px;
    text-transform: uppercase;
  }
  &__brand {
    color: @brand-primary;
    font-size: 21px;
    line-height: 1;
    font-weight: bold;
    margin-bottom: 15px;
  }
  &__img-wrapper {
    background-color: #fff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    width: 157px;
    height: 157px;
    margin: 0 auto 15px;
  }
  &__img {
    margin-left: -8px;
  }
  &__price {
    color: @brand-red;
    font-size: 23px;
    line-height: 1;
    margin-bottom: 15px;
  }
}
.car-item:hover .btn,
.car-item.selected .btn {  
  background-color: #d78400;
  color: #fff;
}
@media @mobile {
  .cars-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1%;
  }
  .car-item {
    flex: 0 1 auto;
    width: 48%;
  }
}
@media @tablet-landscape {
  .car-item {
    width: 31.33%;
  }
}