.follow-wrapper {
  background-color: #f2f2f2;
  border-radius: 25px;
  display: inline-block;
  margin: 0 15px 15px;
  padding: 8px 12px;
}
.follow-label {
  color: @brand-primary;
  display: inline-block;
  vertical-align: middle;
  margin: 0 40px 0 5px;
}
.follow {
  display: inline-block;
  vertical-align: middle;
  list-style: none;
  margin: 0;
  padding: 0;
  &__item {
    display: inline-block;
    vertical-align: middle;
    margin: 0 2px;
  }
  &__link {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: 20px 20px;
    border-radius: 50%;
    display: block;
    width: 36px;
    height: 36px;
    transition: transform .3s linear;
    &:hover {
      transform: scale(1.5);
    }
    &--facebook {
      background-color: #3b5998;
      background-image: data-uri('../img/follow/facebook.svg');
    }
    &--twitter {
      background-color: #55acee;
      background-image: data-uri('../img/follow/twitter.svg');
    }
    &--youtube {
      background-color: #cd201f;
      background-image: data-uri('../img/follow/youtube.svg');
    }
    &--googleplus {
      background-color: #dc4e41;
      background-image: data-uri('../img/follow/googleplus.svg');
    }
  }
  &__title {
    .sr-only;
  }
  & svg {
  }
}