.tours-list-section {
  padding: 0;
  & .page-heading {
    padding: 0 20px;
  }
}
.tour-row {
  margin-bottom: 20px;
}
.tour-item {
  background-color: @gray;
  padding: 20px;
  &--main {
    padding-bottom: 10px;
    @media @mobile {
      display: flex;
    }
  }
  &__img {
    background-repeat: no-repeat;
    background-size: cover;
    flex: 0 0 auto;
    position: relative;
    height: 190px;
    @media @mobile {
      width: 33%;
    }
  }
  &__body {
    color: @brand-primary;
    display: flex;
    flex: 1 1 100%;
    flex-direction: column;
    @media @mobile {
      padding-left: 20px;
    }
  }
  &__title {
    font-size: 24px;
    margin: 15px 0 0;
    text-transform: uppercase;
    @media @mobile {
      margin-top: 5px;
    }
  }
  &__subtitle {
    font-size: 15px;
    margin: 0 0 15px;
  }
  &__description {
    flex: 1 1 auto;
    margin-bottom: 5px;
  }
  &__footer {
    display: flex;
  }
  &__deal {
    position: relative;
    .tours-grid__item & {
      position: absolute;
      bottom: 10px;
      right: 10px;
      z-index: 1;
    }
  }
  &__deal-content {
    background-color: #fff;
    border: 5px solid #fff;
    box-shadow: 0 0 18px rgba(0,0,0,.75);
    position: absolute;
    top: -44px;
    right: 100%;
    margin-right: 25px;
    opacity: 0;
    transition: all .3s ease .3s;
    visibility: hidden;
    width: 460px;
    max-width: calc(~'100vw - 125px');
    z-index: 1;
    @media @tablet {
      max-width: calc(~'100vw - 400px');
    }
    &::after {
      content: '';
      border-left: 20px solid #fff;
      border-top: 20px solid transparent;
      border-bottom: 20px solid transparent;
      filter: drop-shadow(4px 0 2px rgba(0, 0, 0, 0.3));
      position: absolute;
      left: calc(~'100% + 5px');
      top: 35px;
    }
    .tour-item__deal:hover & {
      opacity: 1;
      visibility: visible;
    }
  }
  &__deal-img {
    background-position: 50%;
    background-size: cover;
    padding-bottom: 52%;
  }
  &__deal-title {
    color: #555;
    font-size: 25px;
    line-height: 1;
    margin: 20px 15px 10px;
    text-transform: uppercase;
  }
  &--aside {
    display: flex;
    flex-direction: column;
    min-width: 260px;
    padding-top: 10px;
    & .btn {
      flex: 0 0 auto;
    }
  }
  &__price-list {
    flex: 1 1 auto;
    list-style: none;
    margin: 0 0 20px;
    padding: 0;
    & li {
      color: @brand-primary;
      font-size: 21px;
    }
    & .label {
      display: inline-block;
      min-width: 100px;
    }
    & .value {
      color: #c70f14;
    }    
  }
  @media @tablet {
    padding: 30px 20px;
    &--aside {
      padding: 30px;
    }
  }
}
