// Pagination
.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  list-style: none;
  margin: 30px 0 0;
  padding: 0;
  text-align: right;
  & li {
    display: inline-block;
    vertical-align: middle;
  }
  &__item {
    border: 1px solid #dadada;
    color: @brand-primary;
    display: block;
    font-size: 18px;
    line-height: 36px;
    margin-left: -1px;
    text-align: center;
    text-decoration: none;
    width: 38px;
    height: 38px;
    transition: all .3s;
    &:hover,
    &.current {
      background-color: @brand-primary;
      color: #fff;
    }
  }
}
.pagination-previous,
.pagination-next {
  color: #b4b4b4;
  display: block;
  font-size: 18px;
  line-height: 36px;
  margin-left: -1px;
  text-align: center;
  text-decoration: none;
  width: 38px;
  height: 38px;
  transition: all .3s;
  transform: scaleY(1.8);
  transform-origin: 0 40%;
  &:hover,
  &:focus {
    color: @brand-primary;
  }
}
.pagination-previous:before {
  content: '\003c';
}
.pagination-next:after {
  content: '\003e';
}

// Ribbon
.ribbon {
  left: -5px; top: -5px;
  overflow: hidden;
  position: absolute;
  text-align: center;
  width: 140px; 
  height: 140px;
  z-index: 1;
  & span {
    background-color: @brand-green;
    box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
    color: #fff;
    display: block;
    font-size: 18px;
    line-height: 1;
    padding: 11px 0;
    position: absolute;
    top: 34px;
    left: -41px;
    text-align: center;
    text-transform: uppercase;
    transform: rotate(-45deg);
    width: 190px;
    &::before {
      content: "";
      position: absolute; left: 0px; top: 100%;
      z-index: -1;
      border-left: 3px solid #006700;
      border-right: 3px solid transparent;
      border-bottom: 3px solid transparent;
      border-top: 3px solid #006700;
    }
    &::after {
      content: "";
      position: absolute; right: 0px; top: 100%;
      z-index: -1;
      border-left: 3px solid transparent;
      border-right: 3px solid #006700;
      border-bottom: 3px solid transparent;
      border-top: 3px solid #006700;
    }
  }
}

// Sort Bar
.sort-bar-wrapper {
  background-color: @gray;
}
.sort-bar {
  margin: auto;
  max-width: @section-max-width;
  padding: 6px 0;
  @media @tablet {
    display: flex;
  }
}

.sort-options {
  color: @brand-primary;
  display: flex;
  flex: 1 1 auto;
  margin: 0;
  text-transform: uppercase;
  &__label {
    flex: 0 0 auto;
    font-size: 14px;
    line-height: 1.43;
    padding: 5px;
    text-align: center;
    width: 30%;
    @media @tablet {
      width: 15%;
    }
  }
  & dd {
    border-left: 1px solid @brand-primary;
    margin: 0;
    text-align: center;
    & button {
      font-size: 14px;
      line-height: 1.43;
      padding: 5px 35px 5px 10px;
      position: relative;
      text-transform: uppercase;
      &::before,
      &::after {
        content: '';
        display: inline-block;
        border-right: 7px solid transparent;
        border-left: 7px solid transparent;
        position: absolute;
        top: calc(~'50% - 3px');
        right: 0;
      }
      &::before {
        border-top: 8px solid @brand-primary;
        right: 14px;
      }
      &::after {
        border-bottom: 8px solid @brand-primary;
      }
      &.ascending::after {
        display: none;
      }
      &.descending::before {
        display: none;
      }
      &.descending::after {
        right: 14px;
      }
    }
  }
  &__name {
    flex: 1 1 auto;
    width: 35%;
    @media @tablet {
      width: 50%;
    }
  }
  &__price {
    flex: 1 1 auto;
    width: 35%;
    @media @tablet {
      width: 20%;
    }
  }
  &__activity {
    padding: 0 20px;
  }
}

// Selection View
.selection-view {
  list-style: none;
  margin: 0 0 10px;
  padding: 0 20px;
  @media @tablet {
    margin: 0;
  }
  & li {
    display: inline-block;
    vertical-align: top;
  }
  &__item {
    display: block;
    padding: 7px;
    transition: background-color .3s;
    &:hover,
    &.active {
      background-color: @brand-primary;
      & svg {
        fill: #fff;
      }
    }
    & svg {
      transition: fill .3s;
      fill: @brand-primary;
      display: block;
    }
  }
}

// Week Days
.week-days {
  flex: 1 1 auto;
  list-style: none;
  margin: 0;
  padding: 0;
  & li {
    background-repeat: no-repeat;
    background-position: 50% 0;
    display: inline-block;
    vertical-align: top;
    font-size: 13px;
    margin: 5px 10px 0 0;
    padding-top: 15px;
    &:last-child {
      margin-right: 0;
    }
    &.available {
      background-image: data-uri('../img/icon-check.svg');
      background-size: auto 11px;
    }
    &.unavailable {
      background-image: data-uri('../img/icon-close.svg');
    }
  }
}

// Embeds responsive
// Credit: Nicolas Gallagher and SUIT CSS.
.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;

  .embed-responsive-item,
  iframe,
  embed,
  object,
  video {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    border: 0;
  }
}
// Modifier class for 16:9 aspect ratio
.embed-responsive-16by9 {
  padding-bottom: 56.25%;
}
// Modifier class for 4:3 aspect ratio
.embed-responsive-4by3 {
  padding-bottom: 75%;
} 

// Link Blocks
.link-block {
  color: #fff;
  display: block;
  margin-bottom: 20px;
  position: relative;
  text-decoration: none;
  &:hover,
  &:focus {
    color: #fff;
  }
  &__embed {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 45%;
    position: relative;
    &--square {
      padding-bottom: 100%;
    }
    &--bordered {
      border: 6px solid @gray;
    }
  }
  &__img {
    vertical-align: top;
  }
  &__price {
    font-size: 26px;
    position: absolute;
    top: 15px;
    left: 15px;
    text-shadow: 0 0 4px #000;
    font-size: 26px;
    font-weight: bold;
  }
  &__price-label {
    font-size: 20px;
    line-height: 1;
  }
  &__price-count {
    font-size: 26px;
    line-height: 1;
  }
  &__body {
    background-color: rgba(0,0,0,.5);
    padding: 10px 15px 20px;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    &:before {
      content: '';
      position: absolute;
      bottom: 100%;
      left: calc(~'50% - 16px');
      border-right: 16px solid transparent;
      border-bottom: 16px solid rgba(0,0,0,.5);
      border-left: 16px solid transparent;
    }
  }
  &__title {
    font-size: 18px;
    line-height: 1;
    margin: 0;
    text-transform: uppercase;
  }
  &__description {
  
  }
  &__number {
    background-color: rgba(255,255,255,.8);
    border-radius: 50%;
    color: @brand-primary;
    font-size: 19px;
    font-weight: 700;
    line-height: 32px;
    text-align: center;
    width: 32px;
    position: absolute;
    top: 3px;
    left: 3px;
  }
  &__main {
    background-color: rgba(0,0,0,.4);
    font-size: 15px;
    max-width: 100%;
    overflow: hidden;
    padding: 2px 10px 2px 20px;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: absolute;
    bottom: 5px;
    left: 0;
  }
}
@media @mobile {
  .link-blocks {
    display: flex;
    margin: 0 -9px;
  }
  .link-block {
    flex: 1 1 0%;
    margin-right: 9px;
    margin-left: 9px;
  }
}
@media @mobile-portrait {
  .link-block__body {
    padding: 10px;
  }
  .link-block__title {
    font-size: 16px;
  }
  .link-block__description {
    display: none;
  }
}
@media @tablet-landscape {
  .link-block__title {
    font-size: 23px;
  }
}
@media @tablet-portrait {
  .flex-col-2-3 .link-blocks {
    display: block;
  }
}

// Customer Steps
.customer-steps {
  border: 1px solid #d3d3d3;
  list-style: none;
  margin: 40px 0 25px;
  padding: 10px 10px 0;
  text-align: center;
}
.customer-step {
  display: inline-block;
  margin-bottom: 10px;
  text-transform: uppercase;
  position: relative;
    &--first,
    &--second {
    padding-right: 70px;
    &::before,
    &::after {
      content: '';
      background-color: #d2d2d2;
      border-radius: 50%;
      width: 16px;
      height: 16px;
      position: absolute;
      top: calc(~'50% - 8px');
      right: 15px;
    }
    &::after {
      right: 35px;
    }
  }
  &__title {
    display: inline-block;
    vertical-align: middle;
    .customer-step--current & {
      font-weight: bold;
    }
  }
  &__count {
    background-color: #d2d2d2;
    border-radius: 50%;
    color: #fff;
    display: inline-block;
    font-size: 52px;
    font-weight: 700;
    line-height: 77px;
    position: relative;
    width: 77px;
    height: 77px;
    text-align: center;
    vertical-align: middle;
    &--first,
    &--second {
      margin-left: 10px;
      &::after {
        content: '';
        border-left: 16px solid #d2d2d2;
        border-top: 9px solid transparent;
        border-bottom: 9px solid transparent;
        position: absolute;
        top: calc(~'50% - 9px');
        left: calc(~'100% - 2px');
      }
    }
    &--first {
      background-image: data-uri('../img/icon-check-white.svg');
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: 45px auto;
    }
    .customer-step--current & {
      background-color: @brand-primary;
      &::after {
        border-left-color: @brand-primary;
      }
    }
  }
}
.icon-lock {
  fill: #d2d2d2;
  height: 45px;
  margin: 0 5px 0 10px;
  vertical-align: -13px;
  width: 31px;
  .customer-step--current & {
    fill: @brand-primary;
  }
}

// Tour Item
.tour-block {
  display: block;
  padding: 0 15px 15px;
  &__header {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 180px;
    margin: 0 -15px 10px;
    position: relative;
    .tour-block:hover &::after,
    .selected &::after {
      content: '';
      border: 4px solid @selected-border-color;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      pointer-events: none;
    }
  }
  &__content {
    background-color: rgba(0,0,0,0.6);
    color: #fff;
    padding: 10px 15px;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }
  &__title {
    font-size: 17px;
    text-transform: uppercase;
  }
  &__description {
    font-size: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &__price {
    background-color: rgba(0,0,0,0.6);
    color: #fff;
    font-size: 19px;
    line-height: 1;
    padding: 10px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  &__price-label {
    font-size: 15px;
  }
  &__footer {
    text-align: center;
  }
  &:hover .btn-notice,
  &.selected .btn-notice {
    background-color: #d78400;
    color: #fff;
  }
}
