// Airport Transfers
.airport-transfers-section {
  background-color: @gray;
  margin-top: 2px;
  padding: 10px 20px 20px;
}
.airport-transfers-title {
  color: @brand-primary;
  font-size: 28px;
  text-align: center;
  margin: 8px 0;
}
.airport-transfers-list-item {
  border: 3px solid #fff;
  margin-bottom: 2px;
  padding: 5px 10px 10px;
  &__title {
    color: @brand-primary;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    margin: 0 0 15px;
  }
  &__image-wrapper {
    background-color: #fff;
    height: 220px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__label {
    display: block;
    font-size: 14px;
    line-height: 20px;
    margin: 8px 0 4px;
    text-align: center;
  }
  &__select {
    width: 100%;
  }
}
@media @mobile {
  .airport-transfers-list {
    display: flex;
    flex-wrap: wrap;
  }
  .airport-transfers-list-item {
    flex: 1 1 auto;
    width: 49.6%;
    margin: 0.2%;
  }
}
@media @tablet-landscape {
  .airport-transfers-list-item {
    width: 24.6%;
  }
}

// Top Tours
.top-tours {
  &__item {
    background-color: @gray;
    margin-bottom: 20px;
  }
}
.page-subsection-title {
  font-size: 25px;
  font-weight: normal;
  margin: 0 0 30px;
  text-transform: uppercase;
}
.show-all-button {
  float: right;
}
.top-tours-info {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 0 0 auto;
  min-height: 180px;
  position: relative;
  &__destination {
    color: #fff;
    font-weight: normal;
    font-size: 17px;
    margin: 0;
    position: absolute;
    top: 15px;
    right: 0;
    left: 0;
    text-align: center;
    text-shadow: 2px 2px 5px #000;
  }
  &__price {
    background-color: rgba(0,0,0,0.3);
    color: #fff;
    padding: 5px;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    text-align: center;
    &-label {
      font-size: 20px;
      vertical-align: top;
    }
    &-badge {
      font-size: 16px;
    }
    &-count {
      font-size: 30px;
    }
    &-currency {
      font-size: 26px;
    }
  }
}
.top-tours-description {
  flex: 1 1 auto;
  padding: 15px 20px 55px 20px;
  position: relative;
  &__title {
    font-size: 23px;
    font-weight: normal;
    margin: 0;
    & a {
      color: @brand-primary;
    }
  }
  &__subtitle {
    color: #737373;
    font-size: 14px;
    font-weight: normal;
    margin: 0;
  }
  &__text {
    border-top: 1px solid #9e9e9e;
    font-size: 14px;
    line-height: 18px;
    margin: 7px 0;
    padding-top: 7px;
    min-height: 43px;
    overflow: hidden;
  }
  &__button {
    position: absolute;
    right: 20px;
    bottom: 15px;
  }
}

// Recent Articles
.recent-articles {
  &__item {
    display: flex;
    margin-bottom: 50px;
  }
}
.recent-article-tile {
  flex: 0 0 auto;
  max-width: 40%;
  min-height: 166px;
  position: relative;
  &__image {
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    width: 150px;
    padding-bottom: 100%;
    border-radius: 50%;
    max-width: 100%;
  }
  &__title {
    background-color: #e6e6e6;
    color: #07284c;
    font-size: 15px;
    line-height: 20px;
    padding: 3px;
    text-align: center;
    position: absolute;
    top: 140px;
    right: 0;
    left: 0;
  }
}
.recent-article-info {
  flex: 1 1 auto;
  padding: 0 0 30px 20px;
  position: relative;
  &__title {
    border-bottom: 1px solid #9d9d9d;
    font-size: 19px;
    font-weight: normal;   
    margin: 15px 0 7px;
    padding-bottom: 7px;
    & a {
      color: @brand-primary;
    }
  }
  &__date {
    color: #737373;
    font-size: 14px;
    margin-bottom: 5px;
  }
  &__description {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 7px;
    min-height: 36px;
    overflow: hidden;
  }
  &__button {
    position: absolute;
    right: 0;
    bottom: 0;
  }
}
@media @mobile {
  .top-tours {
    &__item {
      display: flex;
    }
  }
  .top-tours-info {
    width: 190px;
  }
}
@media @tablet-landscape {
  .home-page-columns {
    display: flex;
  }
  .top-tours {
    flex: 1 1 auto;
    padding-right: 20px;
    width: 50%;
  }
  .recent-articles {
    flex: 1 1 auto;
    padding-left: 20px;
    width: 50%;
  }
}

// Airport Transfers
.transfer-item {
  border: 1px solid #dedede;
  cursor: pointer;
  margin-bottom: 20px;
  &:hover {
    border-color: #7ad588;
  }
  &--active {
    border-color: #7ad588;
    cursor: default;
    outline: 3px solid #7ad588;
  }
  &__column {
    padding: 15px;
  }
  &__column-one {
    text-align: center;
  }
  &__label {
    color: @brand-primary;
    font-size: 20px;
    line-height: 1;
    margin: 0 0 20px;
    text-transform: uppercase;
  }
  &__column-two {
    position: relative;
  }
  &__logo-wrapper {
    border-bottom: 1px solid #dedede;
    margin-top: 10px;
    padding-bottom: 5px;
    text-align: center;
  }
  &__note {
    font-size: 12px;
    padding: 5px 15px;
    text-align: right;
  }
    &__add-to-cart {
    display: none;
    margin: 10px auto 0;
    .transfer-item--active & {
      display: block;
    }
  }
  &__column-three {
    background-color: @gray;
    position: relative;
    text-align: center;
  }
  &__name {
    font-size: 17px;
    margin: 0;
  }
  &__amount-label {
    font-size: 13px;
  }
  &__amount {
    color: #c00000;
    margin: 10px 0 20px;
    &-badge {
      font-size: 32px;
    }
    &-count {
      font-size: 37px;
    }
    &-currency {
      font-size: 28px;
    }
  }
  &__button {
    width: 165px;
  }
  &__options {
    display: none;
    padding: 35px 25px 15px;
    .transfer-item--active & {
      display: block;
    }
  }
}
@media @tablet {
  .transfer-item {
    &__row {
      display: flex;
    }
    &__column {
      flex: 0 0 auto;
    }
    &__column-one {
      // width: 340px;
      width: 30%;
      // padding: 15px 0 0 35px;
      .transfer-item--active & {
        border-bottom: 1px solid #dedede;
      }
    } 
    &__column-two {
      flex: 1 1 auto;
      margin-right: 10px;
      .transfer-item--active & {
        border-bottom: 1px solid #dedede;
      }
    }
    &__logo-wrapper {
      max-width: 335px;
      text-align: left;
    }
    &__add-to-cart {
      margin: 10px 0 0;
    }
    &__column-three {
      padding: 10px 25px;
      &:before {
        content: '';
        border-right: 22px solid @gray;
        border-top: 15px solid transparent;
        border-bottom: 15px solid transparent;
        position: absolute;
        top: 50%;
        right: 100%;
        margin-top: -15px;
      }
    }
  }
  .transfer-form-group {
    flex: 1 1 auto;
    margin: 0 10px;
  }
  .airline-form-field {
    width: 60%;
  }
  .flight-no-form-field {
    width: 40%;
    // padding-left: 10px;
  }
}
@media @tablet-landscape {
  .transfer-item {
    &__note {
      margin-top: -15px;
    }
    &__add-to-cart {
      position: absolute;
      top: 10px;
      right: 10px;
    }
  }
}